#home
{
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right bottom,rgba(98, 198, 198, 0.7),rgba(237, 223, 223, 0.5));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#intro
{
  width: 90%;
  height: 50%;
  background-color: transparent;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

#introP
{
  width: 50%;
  height: 100%;
  background-color: transparent;
  font-size: 25px;
  font-weight: 700;
  color: black;
  line-height: 230%;
}

#introP h1:first-child:hover
{
  color: rgba(39, 149, 216, 0.7);
  transition: 0.5s;
}

#introP h2:hover
{
  color: rgba(24, 151, 229, 0.7);
  transition: 0.5s;
}

#introP h1:nth-child(5):hover
{
  color: rgba(12, 206, 64, 0.7);
  transition: 0.5s;
}

#introP h1:nth-child(5):hover
{
  color: rgba(12, 206, 64, 0.7);
  transition: 0.5s;
}

#introP h3:nth-child(7):hover
{
  color: rgba(102, 53, 180, 0.7);
  transition: 0.5s;
}



#intro img
{
  width: 25%;
  height: 100%;
  border-radius: 50%;
}

#contactArea
{
  width: 80%;
  height: 30%;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content:flex-start;
  text-align: center;
  font-size: 30px;
}

a:hover
{
  color: blue;
  transition: 1s;
}


