#portfolio
{
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right bottom,rgba(16, 30, 215, 0.7),rgba(237, 223, 223, 0.5));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#portfolioTitle
{
  width: 100%;
  height: 10%;
  line-height: 500%;
  padding-left: 50px;
}

#portfolioMain
{
  width: 100%;
  height: 90%;
  position: relative;
}

.portItem
{
  position: absolute;
  width: 90%;
  height: 80%;
  border-radius: 10px;
  border: 1px solid rgba(3, 15, 177, 0.7);
  background-image: linear-gradient(to right bottom,rgba(7, 22, 232, 1),rgba(237, 223, 223, 1));
  padding-left: 20px;
  transition: 0.5s;
}

.portItem:nth-child(2)
{
  border: 1px solid rgba(162, 91, 167, 0.708);
  background-image: linear-gradient(to right bottom,rgb(217, 7, 232),rgba(237, 223, 223, 1));
}

.portItem:nth-child(3)
{
  border: 1px solid rgba(197, 75, 106, 0.708);
  background-image: linear-gradient(to right bottom,rgb(225, 21, 106),rgba(237, 223, 223, 1));
}

.portItem:nth-child(4)
{
  border: 1px solid rgba(167, 95, 95, 0.708);
  background-image: linear-gradient(to right bottom,rgb(232, 7, 7),rgba(237, 223, 223, 1));
}


.portItem h2
{
  margin-top: 20px;
}

.InfoWrap
{
  display: flex;
  justify-content: center;
}

.portItem .portImgWrap
{
  margin-top: 30px;
  margin-left: 30px;
  width: 70%;
  height: 80%;
}

.portItem .portInfo
{
  margin-top: 30px;
  width: 20%;
  height: 80%;
  font-size: 16px;
  font-weight: 700;
}

.portInfo li
{
  margin: 15px 0;
}

.portInfo a 
{
  position: relative;
  left: 300px;
  bottom: 60px;
  font-size: 20px;
  color: white;
  letter-spacing: 2px;
}

.portInfo a:hover
{
  color: rgb(18, 18, 205);
  transition: 0,5s;
}

.portImgWrap img
{
  width: 483px;
  height: 262px;
  margin: 10px;
}

.portItem:first-child
{
  top: 40px;
  left: 60px;
  z-index: 4;
}

.portItem:nth-child(2)
{
  top: 60px;
  left: 80px;
  z-index: 3;
}

.portItem:nth-child(3)
{
  top: 80px;
  left: 100px;
  z-index: 2;
}

.portItem:last-child
{
  top: 100px;
  left: 120px;
  z-index: 1;
}

.button
{
  position: absolute;
  bottom: 20px;
  right: 0;
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-left: 40px solid rgb(52, 50, 50);
  cursor: pointer;
}
