#history
{
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right bottom,rgba(77, 84, 177, 0.7),rgba(237, 223, 223, 0.5));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#historyTitle
{
  width: 100%;
  height: 10%;
  line-height: 500%;
  padding-left: 50px;
}

#historyMain
{
  width: 100%;
  height: 90%;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  align-items: center;
  justify-items: center;
}

.item
{
  width: 80%;
  height: 70%;
  border-radius: 10px;
  border: 1px solid rgba(15, 26, 179, 0.7);
  background-image: linear-gradient(to right bottom,rgba(6, 15, 138, 0.7),rgba(237, 223, 223, 0.5));
  padding-left: 20px;
}

.item ul
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 40px;
}

.item ul li
{
  margin: 15px 0;
  font-size: 20px;
}

