#skill
{
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right bottom,rgba(88, 138, 208, 0.7),rgba(237, 223, 223, 0.5));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#skillTitle
{
  width: 100%;
  height: 10%;
  line-height: 500%;
  background-color: transparent;
  padding-left: 50px;
}

#stackWrap
{
  width: 100%;
  height: 90%;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  align-items: center;
  justify-items: center;
}

.stack
{
  position: relative;
  width: 90%;
  height: 70%;
  background-image: linear-gradient(to right bottom,rgba(32, 100, 195, 0.7),rgba(237, 223, 223, 0.5));
  display: flex;
  border-radius: 10px;
} 

.stack ul
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 30px;
  font-size: 20px;
}

.stack::before
{
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 99%;
  border: 1px solid rgba(27, 93, 115, 0.5);
  border-radius: 10px;
}


.stack img
{
  
  width: 140px;
  height: 147px;
  border-radius: 10px;
  background-color: white;
}
