nav
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: cneter;
  width: 10%;
  height: 100%;
  background-image: linear-gradient(to right bottom,rgb(197, 234, 247,0.7),rgba(33, 110, 204, 0.5));
}

nav a 
{
  display: inline-block;
  width: 100%;
  height: 15%;
  text-align: center;
  line-height: 470%;
  font-size: 30px;
  border-bottom: 1px solid rgb(84, 110, 121);
  border-bottom-right-radius: 10px;
}

nav a:first-child
{
  border-top: 1px solid rgb(84, 110, 121);
  border-top-left-radius: 10px;
}