
#contact
{
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right bottom,rgba(150, 30, 214, 0.7),rgba(237, 223, 223, 0.5));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#contactTitle
{
  width: 100%;
  height: 10%;
  line-height: 500%;
  padding-left: 50px;
}

#contactInfo
{
  width: 100%;
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#contactInfo span
{
  margin: 30px 0;
}

#iconWrap
{
  width: 50%;
  height: 50%;
  background-image: linear-gradient(to right bottom,rgba(86, 20, 122, 0.7),rgba(237, 223, 223, 0.5));
  border: 1px solid rgba(141, 36, 198, 0.7);
  text-decoration: none;
  color: rgb(72, 10, 166);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 150%;
  border-radius: 10px;
}

#iconWrap span
{
  font-size: 25px;
  font-weight: 600;
}

.footerIcon
{
  line-height: 25px;
}